.social-links {
  margin-top: 5em;
  
  ul {
    display: flex;
    @include justify-content(space-around);
    @include align-items(center);
  }

  li {
    margin: 0 5px;
  }

  a {
    display: block;
    font-size: 2.2em;
    border-radius: 2em;
    padding: .3em;

    &:link {
      color: $white;
    }
  }

  @include media($screen-small-up) {
    @include span-columns(6);
    @include shift(3);
  }
}

@each $color in $socialPalette {
  .social-#{nth($color, 1)} {
    background-color: nth($color, 2);

    &:hover, &.active {
      background-color: darken(nth($color, 2), 10%);
    }
  }
}